.profile{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 50px;
}

@media screen and (max-width: 690px) {
    .profile{
        flex-direction: column;
        padding-top: 42px;
        padding-bottom: 36px;
    }
}